import React from 'react'
import './works.styles.scss'

import WORKS_DATA from './works.data'

function Works() {

    const works = WORKS_DATA

    return (

        <div className='works'>
            <div className="works-inner">

                <div className="works-inner-up">
                    {
                        works
                        .filter((work, id) => id < 3)
                        .map(
                            work => (
                                <div className="work-item"
                                key={work.id}>
                                    <div 
                                    className={`work-item-image`} 
                                    style={{
                                    backgroundImage: `url(${work.img})`
                                    }}></div>
                                    <div className="work-item-description">
                                        <div className={`${work.name}-description`}>
                                            <h3>{work.name.toUpperCase()}</h3>
                                            <p>{work.description}</p>
                                        </div>
                                    </div>
                                </div>
                            )
                        )
                    }
                </div>

                <div className="works-inner-down">
                {
                        works
                        .filter((work, id) => id > 2)
                        .map(
                            work => (
                                <div className="work-item"
                                key={work.id}>
                                    <div 
                                    className={`work-item-image`} 
                                    style={{
                                    backgroundImage: `url(${work.img})`
                                    }}></div>
                                    <div className="work-item-description">
                                        <div className={`${work.name}-description`}>
                                            <h3>{work.name.toUpperCase()}</h3>
                                            <p>{work.description}</p>
                                        </div>
                                    </div>
                                </div>
                            )
                        )
                    }
                </div>

            </div>
        </div>
    )
}

export default Works
