import React from 'react'
import LOGOS_DATA from './logos-data'
import './logos.styles.scss'

function Logos() {

    const logos = LOGOS_DATA;


    return (
        <div className='logos'>
            <div className="logos-inner">
                <div className="logos-up">
                    {
                        logos
                        .filter((logo, id) => id < 4)
                        .map(logo => (
                            <div className="logos-item" key={logo.id} styles={{width: '50px'}}>
                                <img src={logo.img} alt="" style={{width: '70px'}} />
                            </div>
                        ))
                    }
                </div>
                <div className="logos-down">
                {
                        logos
                        .filter((logo, id) => id > 3)
                        .map(logo => (
                            <div className="logos-item" key={logo.id}>
                                <img className='logos-item-img' src={logo.img} alt="" style={{width: '50px'}} />
                            </div>
                        ))
                    }
                </div>
            </div>
        </div>
    )
}

export default Logos
